import { Link, useNavigate } from "react-router-dom";

const Header = ({ user, onLogin }) => {
  const navigate = useNavigate();
  const handleClick = () => {
    onLogin(null);
    navigate("/");
  };
  return (
    <header>
      <nav>
        <Link to="/"> </Link>
        {user && (
          <div>
            <span>{user.nickname}</span>
            <button onClick={handleClick}></button>
          </div>
        )}
      </nav>
    </header>
  );
};

export default Header;
