import { useEffect, useState } from "react";
import { supabase } from "../supabaseClient";
import { Link } from "react-router-dom";

const PostList = () => {
  const POSTS_PAGE = 10;
  const [posts, setPosts] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
  const [current, setCurrenet] = useState(1);

  const fetchPosts = async () => {
    //supabase   (    )
    // const { data, error } = await supabase
    //   .from("pb_posts")
    //   .select("id,title,users(nickname)");
    // if (!error) {
    //   setPosts(data);
    // }

    //    .range (, ) page:1 = 0~9, page:2 = 10~13
    const from = (current - 1) * POSTS_PAGE;
    const to = from + POSTS_PAGE - 1;

    const { data, error } = await supabase
      .from("pb_posts")
      .select("id,title,pb_users(nickname)")
      .order("id", { ascending: false }) //gpt   ()
      .range(from, to);
    if (!error) {
      setPosts(data);
    }
  };

  const fetPostCount = async () => {
    const { count } = await supabase
      .from("pb_posts")
      .select("id", { count: "exact", head: true });
    setTotal(count);
    // const {count} = await supabase.from("pb_posts").select('*');
    // console.log(count); ->        (0808)
    setPage(Math.ceil(count / POSTS_PAGE));
  };

  useEffect(() => {
    // console.log("");
    fetPostCount();
  }, []);

  useEffect(() => {
    fetchPosts();
  }, [current]);

  return (
    <div id="postlist">
      <div className="list-head">
        {" "}
        <h2> </h2>
        <Link to="/write"></Link>
      </div>
      <ul>
        {posts &&
          posts.map((item) => {
            // console.log(item);
            return (
              <li key={item.id}>
                <Link to={`/post/${item.id}`}>{item.title}</Link>
                <p>{item.pb_users.nickname}</p>
              </li>
            );
          })}
      </ul>
      {/*   */}
      <div className="page-btn">
        {Array(page)
          .fill(null)
          .map((item, idx) => {
            const pageNumber = idx + 1; //idx 0   1  +1 
            return (
              <button
                key={idx}
                onClick={() => {
                  setCurrenet(pageNumber);
                }}
              >
                {pageNumber}
              </button>
            );
          })}
      </div>
    </div>
  );
};

export default PostList;
